.App .ant-input-group-addon {
  padding:0;
  background:#000;
  border:0 none;
}
.App .my-link {
    display: block;
    background: #000;
    border: 0 none;
    margin-left: -2px;
    width:70px;
    height:32px;
    line-height:32px;
}
.ant-card.card-bank {
  border-radius: 5px;
  margin:20px 0;
}
.card-bank .ant-card-head {
  border:0 none;
}
.card-bank .ant-card-head {
  color:#ddd;
}
.card-bank .dele {
  border:1px solid #ddd;
  color:#ddd;
  padding:2px 5px;
  border-radius: 5px;
  font-size: 8px;
}
.card-bank .number {
  font-size: 18px;
  margin:0;
}
.card-bank .ant-card-body {
  padding:0 24px;
}
.add-bank {
  margin : 20px 0;
  padding:20px;
  border-radius: 5px;
  border:1px solid #ffc742;
  font-size:50px;
  color:#ffc742;
  text-align: center;
  cursor: pointer;
}

