.App .activity-main .ant-tabs-nav .ant-tabs-tab {
    width:25%;
}
.pop dl {
    font-size:12px;
    line-height: 22px;
}
.pop dl > dt {
    color:#ffc742;
    float: left;
}
.pop table  {
    width:100%;
    margin:15px 0;
}
.pop table tr:first-child {
    background:#333;
}
.pop table tr:nth-child(2) td:first-child ,
.pop table tr:nth-child(3) td:first-child {
    background:none;
}
.pop table td {
    border:1px solid #999;
    text-align: center;
    padding:5px 0;
}